.wheel-and-hamster {
  --dur: 1s;
  width: 12em;
  height: 12em;
  font-size: 8px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.wheel, .hamster, .hamster div, .spoke {
  position: absolute;
}

.wheel, .spoke {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  top: 0;
  left: 0;
}

.wheel {
  z-index: 2;
  background: radial-gradient(100% 100%, rgba(153, 153, 153, 0) 47.8%, #000 48%);
}

.hamster {
  animation: hamster var(--dur) ease-in-out infinite;
  width: 7em;
  height: 3.75em;
  transform-origin: 50% 0;
  z-index: 1;
  top: 50%;
  left: calc(50% - 3.5em);
  transform: rotate(4deg)translate(-.8em, 1.85em);
}

.hamster__head {
  animation: hamsterHead var(--dur) ease-in-out infinite;
  width: 2.75em;
  height: 2.5em;
  transform-origin: 100%;
  background: #f48c25;
  border-radius: 70% 30% 0 100% / 40% 25% 25% 60%;
  top: 0;
  left: -2em;
  box-shadow: inset 0 -.25em #facc9e, inset .75em -1.55em #fce6cf;
}

.hamster__ear {
  animation: hamsterEar var(--dur) ease-in-out infinite;
  width: .75em;
  height: .75em;
  transform-origin: 50% 75%;
  background: #fbb6b6;
  border-radius: 50%;
  top: -.25em;
  right: -.25em;
  box-shadow: inset -.25em 0 #f48c25;
}

.hamster__eye {
  animation: hamsterEye var(--dur) linear infinite;
  width: .5em;
  height: .5em;
  background-color: #000;
  border-radius: 50%;
  top: .375em;
  left: 1.25em;
}

.hamster__nose {
  width: .2em;
  height: .25em;
  background: #f98686;
  border-radius: 35% 65% 85% 15% / 70% 50% 50% 30%;
  top: .75em;
  left: 0;
}

.hamster__body {
  animation: hamsterBody var(--dur) ease-in-out infinite;
  width: 4.5em;
  height: 3em;
  transform-origin: 17%;
  transform-style: preserve-3d;
  background: #fce6cf;
  border-radius: 50% 30% / 15% 60% 40% 40%;
  top: .25em;
  left: 2em;
  box-shadow: inset .1em .75em #f48c25, inset .15em -.5em #facc9e;
}

.hamster__limb--fr, .hamster__limb--fl {
  -webkit-clip-path: polygon(0 0, 100% 0, 70% 80%, 60% 100%, 0% 100%, 40% 80%);
  clip-path: polygon(0 0, 100% 0, 70% 80%, 60% 100%, 0% 100%, 40% 80%);
  width: 1em;
  height: 1.5em;
  transform-origin: 50% 0;
  top: 2em;
  left: .5em;
}

.hamster__limb--fr {
  animation: hamsterFRLimb var(--dur) linear infinite;
  background: linear-gradient(#facc9e 80%, #f98686 80%);
  transform: rotate(15deg)translateZ(-1px);
}

.hamster__limb--fl {
  animation: hamsterFLLimb var(--dur) linear infinite;
  background: linear-gradient(#fce6cf 80%, #fbb6b6 80%);
  transform: rotate(15deg);
}

.hamster__limb--br, .hamster__limb--bl {
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 30%, 70% 90%, 70% 100%, 30% 100%, 40% 90%, 0% 30%);
  clip-path: polygon(0 0, 100% 0, 100% 30%, 70% 90%, 70% 100%, 30% 100%, 40% 90%, 0% 30%);
  width: 1.5em;
  height: 2.5em;
  transform-origin: 50% 30%;
  border-radius: .75em .75em 0 0;
  top: 1em;
  left: 2.8em;
}

.hamster__limb--br {
  animation: hamsterBRLimb var(--dur) linear infinite;
  background: linear-gradient(#facc9e 90%, #f98686 90%);
  transform: rotate(-25deg)translateZ(-1px);
}

.hamster__limb--bl {
  animation: hamsterBLLimb var(--dur) linear infinite;
  background: linear-gradient(#fce6cf 90%, #fbb6b6 90%);
  transform: rotate(-25deg);
}

.hamster__tail {
  animation: hamsterTail var(--dur) linear infinite;
  width: 1em;
  height: .5em;
  transform-origin: .25em .25em;
  background: #fbb6b6;
  border-radius: .25em 50% 50% .25em;
  top: 1.5em;
  right: -.5em;
  transform: rotate(30deg)translateZ(-1px);
  box-shadow: inset 0 -.2em #f98686;
}

.spoke {
  animation: spoke var(--dur) linear infinite;
  background: radial-gradient(100% 100%, #000 4.8%, rgba(153, 153, 153, 0) 5%), linear-gradient(rgba(140, 140, 140, 0) 46.9%, #000 47%, #000 52.9%, rgba(166, 166, 166, 0) 53%) 50% / 99% 99% no-repeat;
}

@keyframes hamster {
  from, to {
    transform: rotate(4deg)translate(-.8em, 1.85em);
  }

  50% {
    transform: rotate(0) translate(-.8em, 1.85em);
  }
}

@keyframes hamsterHead {
  from, 25%, 50%, 75%, to {
    transform: rotate(0);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(8deg);
  }
}

@keyframes hamsterEye {
  from, 90%, to {
    transform: scaleY(1);
  }

  95% {
    transform: scaleY(0);
  }
}

@keyframes hamsterEar {
  from, 25%, 50%, 75%, to {
    transform: rotate(0);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(12deg);
  }
}

@keyframes hamsterBody {
  from, 25%, 50%, 75%, to {
    transform: rotate(0);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(-2deg);
  }
}

@keyframes hamsterFRLimb {
  from, 25%, 50%, 75%, to {
    transform: rotate(50deg)translateZ(-1px);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(-30deg)translateZ(-1px);
  }
}

@keyframes hamsterFLLimb {
  from, 25%, 50%, 75%, to {
    transform: rotate(-30deg);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(50deg);
  }
}

@keyframes hamsterBRLimb {
  from, 25%, 50%, 75%, to {
    transform: rotate(-60deg)translateZ(-1px);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(20deg)translateZ(-1px);
  }
}

@keyframes hamsterBLLimb {
  from, 25%, 50%, 75%, to {
    transform: rotate(20deg);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(-60deg);
  }
}

@keyframes hamsterTail {
  from, 25%, 50%, 75%, to {
    transform: rotate(30deg)translateZ(-1px);
  }

  12.5%, 37.5%, 62.5%, 87.5% {
    transform: rotate(10deg)translateZ(-1px);
  }
}

@keyframes spoke {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(-1turn);
  }
}

